body {
  font-family: 'Raleway', sans-serif;
  letter-spacing: 1px;
  font-size: 1.4rem;
  color: #333;
  overflow-x: hidden;
}

hr {
  width: 50%;
  border-top: 10px;
  border-color: orange;
  background-image: -webkit-linear-gradient(left, #f0f0f0, #8c8c8c, #f0f0f0);
  background-image: linear-gradient(left, #f0f0f0, #8c8c8c, #f0f0f0);
}

i {
  color: #666;
}

h3 {
  position: relative;
  z-index: 0;
}

h3:before {
  content: "";
  position: absolute;
  width: 20%;
  height: 1px;
  bottom: 0;
  left: 40%;
  border-bottom: 2px solid orange;
}

a {
  text-decoration: none;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  color: #333;
  box-shadow: 0px 0px 0px 0px #eee;
}

a:hover {
  color: #000;
  box-shadow: 0px 2px 0px 0px orange;
}

.active {
  color: #000;
  box-shadow: 0px 2px 0px 0px orange;
}

nav {
  position: fixed;
  top: 0px;
  width: 100vw;
  background: #f5f5f5;
  z-index: 100;
}

.nav-item {
  margin-right: 20px;
}

.nav-button {
  background: orange;
  height: 25px;
  width: 25px;
  position: fixed;
  top: 12px;
  right: 20px;
  display: none;
  cursor: pointer;
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

.nav-button.clicked {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  border-radius: 50%;
}

.nav-wrapper {
  padding: 10px 0px;
  width: 90vw;
  max-width: 900px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  height: 25px;
}

.links {
  font-size: 1.2rem;
}

.links>span {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
}

.links>span a {
  width: 100%;
}

.links a {
  padding-right: 10px;
  margin-right: 20px;
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

#wrapper {
  margin: 0 auto;
  width: 90vw;
  max-width: 900px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#hero {
  position: relative;
  text-align: center;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

