#name {
  position: relative;
  font-size: 3rem;
}

#name:before {
  content: "";
  opacity: 0.5;
  position: absolute;
  width: 300px;
  height: 300px;
  left: 50%;
  z-index: -1;
  margin-left: -155px;
  bottom: -130px;
  border-radius: 50%;
  background: orange;
  border-bottom: 2px solid orange;
  -webkit-animation-name: intro-animation;
          animation-name: intro-animation;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

@-webkit-keyframes intro-animation {
  0% {
    opacity: 0;
    margin-left: -50px;
    width: 100px;
    height: 100px;
    left: 50%;
    bottom: -40px;
    border-radius: 50%;
    background: orange;
    border-bottom: 2px solid orange;
  }
  30% {
    opacity: 1;
    width: 1px;
    height: 1px;
    left: 10%;
    bottom: 0px;
    background: orange;
    border-radius: 0;
    border-bottom: 2px solid orange;
  }
  60% {
    opacity: 1;
    width: 1px;
    height: 1px;
    left: 10%;
    bottom: 0px;
    background: orange;
    border-radius: 0;
    border-bottom: 2px solid orange;
  }
  85% {
    margin-left: 0px;
    opacity: 1;
    width: 40%;
    left: 40%;
    height: 1px;
    border-radius: 0;
    bottom: 0px;
  }
  92% {
    margin-left: 0px;
    opacity: 1;
    width: 40%;
    left: 25%;
    height: 1px;
    border-radius: 0;
    bottom: 0px;
  }
  100% {
    margin-left: 0px;
    opacity: 1;
    width: 40%;
    left: 30%;
    height: 1px;
    border-radius: 0;
    bottom: 0px;
  }
}

@keyframes intro-animation {
  0% {
    opacity: 0;
    margin-left: -50px;
    width: 100px;
    height: 100px;
    left: 50%;
    bottom: -40px;
    border-radius: 50%;
    background: orange;
    border-bottom: 2px solid orange;
  }
  30% {
    opacity: 1;
    width: 1px;
    height: 1px;
    left: 10%;
    bottom: 0px;
    background: orange;
    border-radius: 0;
    border-bottom: 2px solid orange;
  }
  60% {
    opacity: 1;
    width: 1px;
    height: 1px;
    left: 10%;
    bottom: 0px;
    background: orange;
    border-radius: 0;
    border-bottom: 2px solid orange;
  }
  85% {
    margin-left: 0px;
    opacity: 1;
    width: 40%;
    left: 40%;
    height: 1px;
    border-radius: 0;
    bottom: 0px;
  }
  92% {
    margin-left: 0px;
    opacity: 1;
    width: 40%;
    left: 25%;
    height: 1px;
    border-radius: 0;
    bottom: 0px;
  }
  100% {
    margin-left: 0px;
    opacity: 1;
    width: 40%;
    left: 30%;
    height: 1px;
    border-radius: 0;
    bottom: 0px;
  }
}

.content-block {
  margin: 70px 0px 70px 70px;
  padding: 10px 0;
}

#job-title {
  font-size: 1rem;
  opacity: 0;
  -webkit-transform: translateY(-150px);
          transform: translateY(-150px);
  -webkit-animation-name: title-drop;
          animation-name: title-drop;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

@-webkit-keyframes title-drop {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes title-drop {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

.content-block {
  display: flex;
}

.about,
.projects,
.contact {
  text-align: center;
  max-width: 500px;
  margin: 30px auto;
  flex-direction: column;
}

.contact {
  margin-bottom: 50px;
}

.left-block {
  width: 30%;
}

.right-block {
  width: 70%;
  margin-top: 8px;
}

.links {
  font-size: 1rem;
  display: flex;
  justify-content: space-around;
  text-align: center;
}

.links .project-link a {
  padding: 0;
  /* box-shadow: inset 0 -1px 0 0 orange; */
  border: 2px solid orange;
  border-radius: 10px;
  padding: 3px;
  text-align: center;
  width: 100%;
  margin: 0 auto;
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

.links .project-link a:hover {
  background: orange;
  color: white;
}

.tech-used {
  margin: 50px auto;
  display: flex;
  justify-content: space-around;
}

.devicon {
  font-size: 2rem;
  /* opacity: 0;
transform: scale(0.5) translateX(-150px); */
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

.devicon.in-view {
  opacity: 1;
  -webkit-transform: scale(1) translateX(0px);
          transform: scale(1) translateX(0px);
}

.devicon:nth-child(0) {
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
}

.devicon:nth-child(1) {
  -webkit-transition-delay: 0.05s;
          transition-delay: 0.05s;
}

.devicon:nth-child(2) {
  -webkit-transition-delay: 0.1s;
          transition-delay: 0.1s;
}

.devicon:nth-child(3) {
  -webkit-transition-delay: 0.15s;
          transition-delay: 0.15s;
}

.devicon:nth-child(4) {
  -webkit-transition-delay: 0.2s;
          transition-delay: 0.2s;
}

.devicon:nth-child(5) {
  -webkit-transition-delay: 0.25s;
          transition-delay: 0.25s;
}

.devicon:nth-child(6) {
  -webkit-transition-delay: 0.3s;
          transition-delay: 0.3s;
}

.devicon:nth-child(7) {
  -webkit-transition-delay: 0.35s;
          transition-delay: 0.35s;
}

.devicon:nth-child(8) {
  -webkit-transition-delay: 0.4s;
          transition-delay: 0.4s;
}

.devicon:nth-child(9) {
  -webkit-transition-delay: 0.45s;
          transition-delay: 0.45s;
}

.contact-list>a {
  margin: 20px;
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

footer {
  width: 100vw;
  text-align: center;
  margin: 100px 0;
}

.close-nav {
  display: none;
  height: 100vh;
  width: 100vw;
  z-index: 10;
  position: fixed;
  cursor: pointer;
}

.open {
  display: block;
}

.border-bottom {
  border-bottom: 1px solid orange;
  padding-bottom: 10px;
  margin: 5px;
}

#projects {
  max-width: 100%;
  width: 100%;
}

.project-cards {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 600px;
  margin: auto;
  justify-content: space-around;
}

.project-card {
  display: flex;
  width: 100%;
  margin: 60px auto 0px auto;
  background: white;
}

.project-card:hover {}

.card-body {
  width: 100%;
  padding: 20px;
  text-align: left;
}

.project-img {
  max-width: 90vw;
  margin: 10px auto;
  width: 50%;
}

@media screen and (max-with: 500px) {}

@media screen and (max-width: 600px) {
  #wrapper {
    margin: 0 auto;
  }
  .nav-links {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 50vh;
    background: white;
    margin-top: 45px;
    position: fixed;
    right: -500px;
    text-align: right;
    top: 0;
    -webkit-transition: all .1s ease-in-out;
    transition: all .1s ease-in-out;
    z-index: 100;
  }
  .nav-links .nav-item>a {
    box-shadow: inset 0 -2px 0 0 orange;
  }
  .nav-on {
    right: 20px;
    width: 100vw;
  }
  .nav-button {
    z-index: 100;
    display: block;
  }
  .content-block {
    flex-direction: column;
    margin: 70px 0;
    text-align: center;
  }
  .content-block .left-block,
  .content-block .right-block {
    width: 100%;
  }
  .devicon {
    font-size: 1.5rem;
  }
  .contact-list {
    display: flex;
    flex-direction: column;
  }
  body {
    font-size: 1.2rem;
  }
  .continue-button {
    bottom: 10%;
  }
  .project-card {
    flex-direction: column;
    max-width: 95vw;
    text-align: center;
  }
  .card-body {
    max-width: 100%;
    text-align: center;
    padding: 0;
  }
}
